@import url("https://fonts.googleapis.com/css?family=Rubik:300,400,500,700");
html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  margin: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block; }

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden],
template {
  display: none; }

a {
  background-color: transparent; }

a:active,
a:hover {
  outline: 0; }

abbr[title] {
  border-bottom: 1px dotted; }

b,
strong {
  font-weight: bold; }

dfn {
  font-style: italic; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

mark {
  background: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -0.5em; }

sub {
  bottom: -0.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

figure {
  margin: 1em 40px; }

hr {
  box-sizing: border-box;
  height: 0; }

pre {
  overflow: auto; }

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0; }

button {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled],
html input[disabled] {
  cursor: default; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

input {
  line-height: normal; }

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto; }

input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: border-box; }

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

legend {
  border: 0;
  padding: 0; }

textarea {
  overflow: auto; }

optgroup {
  font-weight: bold; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

@font-face {
  font-family: 'GoldenYouth';
  src: url("../fonts/goldenyouthscript-regular-webfont.woff") format("woff"), url("../fonts/goldenyouthscript-regular-webfont.woff2") format("woff2");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'MyriadPro';
  src: url("../fonts/MyriadPro-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

.question {
  position: absolute;
  width: 500px;
  height: 50px;
  left: 0;
  top: 0;
  background: #fff;
  border: 1px solid #ccc; }
  .question__wrapper {
    position: relative;
    width: 500px;
    height: 50px; }

.switch {
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
  display: inline-block;
  width: 24px;
  height: 24px; }
  .switch [type="checkbox"], .switch [type="radio"] {
    opacity: 0; }
  .switch__trigger {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #232863;
    transition: background-color .2s ease-in-out; }
    .switch__trigger:before {
      position: absolute;
      content: "";
      height: 9px;
      width: 21px;
      left: 2px;
      top: 2px;
      transition: transform .2s ease-in-out;
      z-index: 2;
      background-color: #fff; }

.switch__input:checked + .switch__trigger {
  background-color: #EA0094; }

.switch__input:checked + .switch__trigger:before {
  transform: translateY(12px); }

.option {
  position: absolute;
  top: 50px;
  left: 5%;
  width: 90%;
  padding: 20px 35px;
  list-style: none;
  box-shadow: 0 30px 60px 0 rgba(32, 31, 91, 0.3);
  border-radius: 5px; }
  .option--invisible {
    display: none; }
  .option__item {
    cursor: pointer;
    font-size: 18px;
    padding: 25px 0; }

.gifffer-play-button {
  display: none; }

.gif {
  pointer-events: none;
  max-width: 400px !important;
  max-height: 300px !important;
  margin: auto; }

.btn {
  pointer-events: auto;
  width: 150px !important;
  height: 50px !important; }

.gif__wrapper {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  overflow: hidden;
  background: #dbf2ff; }

.gif__wrapper--hidden {
  display: none; }
